import {
    MenuFoldOutlined,
    MenuUnfoldOutlined,
    UploadOutlined,
    UserOutlined,
    VideoCameraOutlined,
} from '@ant-design/icons';
import { Menu } from 'antd';
import React from 'react';
import logo1 from "@/assets/1.png"
import logo2 from "@/assets/2.png"
import { asyncRoutes } from '../index';
import { Link } from "react-router-dom"
import useMenu from './useMenu.js';







const Toggle = ({ value, onChange }) => {
    return (
        <div onClick={onChange} className="trigger">
            {
                value
                    ? <MenuUnfoldOutlined />
                    : <MenuFoldOutlined />
            }
        </div>
    )
}
const Logo = ({ value }) => {
    return (

        <div className={`logo ${value ? "small" : ''}`}>
            <img src={value ? logo2 : logo1} />
        </div>

    )
}
function getItem(label, key, icon, children, path) {
    return {
      key,
      icon,
      children,
      label: path ? <Link to={path}>{label}</Link> : label,
    };
  }
function createItems(asyncRoutes){
    let result = [];
    asyncRoutes.forEach(route=> {
        if(!route.hidden){
            if(!route.children){
                result.push(
                    getItem(route.label,route.key,route.icon,null,route.path)
                )
            }else{
                result.push(
                    getItem(route.label,route.key,route.icon,createItems(route.children),route.path)
                )
            }
        }

    });
    return result
}
// console.log(createItems(asyncRoutes));
const items=[]
export default (props) => {
    const [SelectedKey,openKey] =useMenu();
    console.log(SelectedKey);
    
    
    return (
        <>
            <Logo {...props} />
            <Menu
                theme="dark"
                mode="inline"
                defaultSelectedKeys={[SelectedKey]}
                defaultOpenKeys={[openKey]}
                items={createItems(asyncRoutes)}

            />
            <Toggle {...props} />

        </>
    )
}
