

import { HashRouter, BrowserRouter, Route, Routes, Link } from "react-router-dom"

import Login from "./pages/login/index"
import Layout from "../src/pages/layout";
import { asyncRoutes } from "./pages";
import { Provider } from "react-redux";
import store from "./store/index"

function createItems(accessRoutes) {
    let result = [];
    accessRoutes.forEach(route => {
        if (route.path && route.element) {
            result.push(
                <Route key={route.key} path={route.path} element={route.element}></Route>
            )
        }
        if (route.children) {
            route.children.forEach(route => {
                result.push(
                    <Route key={route.key} path={route.path} element={route.element}></Route>
                )
            })
        }
    });
    return result
}

function App() {
    return (
        <HashRouter>
            <Provider store={store}>
            <Routes>
                <Route path='/' element={<Layout />}>
                    {
                        createItems(asyncRoutes)
                    }
                </Route>

                <Route path='/login' element={<Login />} ></Route>
            </Routes>
            </Provider>
        </HashRouter>
    );
}
export default App;
