


import { useSelector,useDispatch } from "react-redux"

export default (props)=>{
    const dispatch = useDispatch();
    let {size} =useSelector(function(state){
        return state.app
    });
    console.log(size);

const add =()=>{
    dispatch({type:"SIZE_ADD",payload:2  })
}
    return(
        <div>
            <h2></h2>
            <h1>{size}</h1>
            <button onClick={add}>++</button>
        </div>
    )
}